/*  global  */
body {
  position: relative;
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 14px;
  font-weight: normal;
  line-height: 18px;
  text-align: left;
  color: #444444;
  background-color: #ffffff;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 768px) {
  body {
    font-size: 16px;
    line-height: 24px;
  }
}
/*  blocks  */
.advantages {
  width: 320px;
  margin: 0 auto;
  box-sizing: border-box;
  padding: 0 20px;
  margin-bottom: 19px;
}
.advantages__title {
  margin: 0;
  margin-bottom: 33px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: normal;
  color: #111111;
  font-size: 36px;
  line-height: 54px;
}
.advantages__list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0;
  padding: 0;
  list-style: none;
}
.advantages__item {
  position: relative;
  margin-bottom: 26px;
}
.advantages__item::before {
  content: "";
  position: absolute;
  top: -2px;
  width: 60px;
  height: 60px;
  text-align: center;
  background-repeat: no-repeat;
  background-position: center;
  background-color: #68b738;
}
.advantages__item--healthy::before {
  background-image: url("../../img/advantage-leaf@1x.png");
}
.advantages__item--easy::before {
  background-image: url("../../img/advantage-powder@1x.png");
}
.advantages__item--convenience::before {
  background-image: url("../../img/advantage-dining@1x.png");
}
.advantages__item--fast::before {
  background-image: url("../../img/advantage-clock@1x.png");
}
.advantages__text {
  margin: 0;
  margin-left: 80px;
}
@media (min-width: 768px) {
  .advantages {
    width: 768px;
    margin: 0 auto;
    padding: 0 30px;
  }
  .advantages__title {
    margin-bottom: 40px;
    font-size: 60px;
    line-height: 60px;
  }
  .advantages__list {
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
  .advantages__item {
    width: 324px;
    box-sizing: border-box;
    margin-top: 31px;
    margin-bottom: 57px;
    padding-top: 114px;
    padding-right: 75px;
    padding-bottom: 43px;
  }
  .advantages__item--healthy::after {
    content: "1";
    right: 95px;
  }
  .advantages__item--easy::after {
    content: "2";
    right: 79px;
  }
  .advantages__item--convenience::after {
    content: "3";
    right: 77px;
  }
  .advantages__item--fast::after {
    content: "4";
    right: 76px;
  }
  .advantages__item::before {
    top: 0;
    width: 80px;
    height: 80px;
  }
  .advantages__item::after {
    position: absolute;
    z-index: -5;
    top: -37px;
    font-family: "Oswald", "Arial", sans-serif;
    font-size: 280px;
    line-height: 280px;
    text-align: center;
    color: #f2f2f2;
  }
  .advantages__text {
    margin-left: 0;
  }
}
@media (min-width: 1280px) {
  .advantages {
    width: 1280px;
    margin: 0 auto;
    margin-bottom: 25px;
  }
  .advantages__item {
    width: 245px;
    margin-bottom: 0;
    padding-right: 0;
  }
  .advantages__item--healthy::after {
    right: 15px;
  }
  .advantages__item--easy::after {
    right: -2px;
  }
  .advantages__item--convenience::after {
    right: -3px;
  }
  .advantages__item--fast::after {
    right: -4px;
  }
  .advantages__item::before {
    top: 2px;
  }
}
